import { useSelector, useDispatch } from "react-redux";
import { addContact } from "../../redux/contactsSlice";            
import { Form, Input, Button } from "./styled";
import { Notify } from 'notiflix/build/notiflix-notify-aio';


export const ContactForm = () => {       

    const contacts = useSelector(state => state.contacts.array); //   '  

    const dispatch = useDispatch();

    const checkNameClone = (newName, newNumber) => { 
        const nameClone = contacts.find((contact) => ( //  '  ',   ( )
          contact.name.toLowerCase() === newName.toLowerCase()
        ));
    
        if(nameClone) {
          Notify.failure(`${newName} is already in contacts`); 
          return;
        } 
        
        dispatch(addContact(newName, newNumber));  //   contactsSlice    
    };


    const handleFormSubmit = (event) => {
        event.preventDefault();

        const name = event.target.elements.name.value;
        const number = event.target.elements.number.value;

        checkNameClone(name, number);
        event.target.reset();
    }


    return (
        <Form onSubmit={handleFormSubmit} autoComplete="off">
            <label>
                Name
                    <Input
                        type="text"
                        name="name"
                        // pattern="^[a-zA-Z--]+(([' -][a-zA-Z-- ])?[a-zA-Z--]*)*$"
                        title="Name may contain only letters, apostrophe, dash and spaces. For example Adrian, Jacob Mercer, Charles de Batz de Castelmore d'Artagnan"
                        placeholder="Oles Honchar"
                        required
                    />
            </label>
            <label>
                Number
                <Input
                    type="tel"
                    name="number"
                    // pattern="\+?\d{1,4}?[-.\s]?\(?\d{1,3}?\)?[-.\s]?\d{1,4}[-.\s]?\d{1,4}[-.\s]?\d{1,9}"
                    title="Phone number must be digits and can contain spaces, dashes, parentheses and can start with +"
                    placeholder="459-12-56"
                    required
                />
            </label>
            <Button>Add contact</Button>
        </Form>
    );
}