import { configureStore } from "@reduxjs/toolkit";
import { contactsReducer } from './contactsSlice';
import { filterReducer } from './filterSlice';
import { 
    persistStore,
    FLUSH,
    REHYDRATE,
    PAUSE,
    PERSIST,
    PURGE,
    REGISTER,
 } from 'redux-persist'; 
 // redux-persist - ,  ,        ,  localStorage   


export const store = configureStore({
    reducer: {
        contacts: contactsReducer,
        filter: filterReducer,
    },
    middleware(getDefaultMiddleware){  //middleware - ,     action-     reducer,       
        return getDefaultMiddleware({
            serializableCheck:{
                ignoreActions: [FLUSH, REHYDRATE, PAUSE, PERSIST, PURGE, REGISTER], //      action- - ,  Redux    
            }, // action -  ,     JSON.stringify -     -
        });
    },
});

export const persistor = persistStore(store); //   index.js